import { Component, Prop, Event, EventEmitter, State, Watch, h, Host } from '@stencil/core';

export interface AutocompleteMenuItem {
  id: string;
  label: string;
  disabled?: boolean;
  selected?: boolean;
}

@Component({
  tag: 'bh-autocomplete-menu',
  styleUrl: 'bh-autocomplete-menu.css',
  shadow: true,
})
export class BhAutocompleteMenu {
  /**
   * Array of menu items
   */
  @Prop() menuItems: AutocompleteMenuItem[] = [];

  /**
   * Whether the menu is visible
   */
  @Prop() visible: boolean = false;

  /**
   * Currently selected item index
   */
  @Prop() selectedIndex: number = -1;

  /**
   * Maximum height of the menu in pixels
   */
  @Prop() maxHeight: number = 280;

  /**
   * Enable multi-select mode with checkmarks
   */
  @Prop() multiSelect: boolean = false;

  /**
   * Search query for highlighting text
   */
  @Prop() searchQuery: string = '';

  /**
   * Event emitted when an item is clicked
   */
  @Event() bhItemClick!: EventEmitter<AutocompleteMenuItem>;

  /**
   * Event emitted when an item is hovered
   */
  @Event() bhItemHover!: EventEmitter<number>;

  @State() isAnimating: boolean = false;
  @State() shouldRender: boolean = false;
  @State() items: AutocompleteMenuItem[] = [];

  @Watch('visible')
  watchVisible(newValue: boolean) {
    if (newValue) {
      this.shouldRender = true;
      this.isAnimating = false;
    } else {
      this.isAnimating = true;
      setTimeout(() => {
        this.shouldRender = false;
        this.isAnimating = false;
      }, 150);
    }
  }

  @Watch('menuItems')
  watchMenuItems(newValue: AutocompleteMenuItem[]) {
    this.items = [...newValue];
  }

  componentWillLoad() {
    this.shouldRender = this.visible;
    this.items = [...this.menuItems];
  }

  private handleItemClick = (item: AutocompleteMenuItem) => {
    if (!item.disabled) {
      if (this.multiSelect) {
        // Toggle selection in multi-select mode
        this.items = this.items.map(i =>
          i.id === item.id ? { ...i, selected: !i.selected } : i
        );
        const updatedItem = this.items.find(i => i.id === item.id)!;
        this.bhItemClick.emit(updatedItem);
      } else {
        this.bhItemClick.emit(item);
      }
    }
  };

  private handleItemMouseEnter = (index: number) => {
    this.bhItemHover.emit(index);
  };

  private handleMouseDown = (e: MouseEvent) => {
    e.preventDefault();
  };

  private getMenuClasses = () => {
    return {
      'autocomplete-menu': true,
      'animate-dropdown-exit': this.isAnimating && !this.visible,
      'animate-dropdown-enter': this.visible && !this.isAnimating,
    };
  };

  private getItemClasses = (index: number, item: AutocompleteMenuItem) => {
    return {
      'autocomplete-item': true,
      'disabled': !!item.disabled,
      'selected': index === this.selectedIndex,
    };
  };

  private getHighlightedText = (text: string) => {
    if (!this.searchQuery || this.searchQuery.length < 3) {
      return text;
    }

    const regex = new RegExp(`(${this.searchQuery})`, 'gi');
    const parts = text.split(regex);

    return parts.map((part, index) =>
      regex.test(part) ? (
        <span key={index} class="highlight">{part}</span>
      ) : (
        <span key={index}>{part}</span>
      )
    );
  };

  render() {
    if (!this.shouldRender || this.items.length === 0) {
      return null;
    }

    return (
      <Host>
        <div
          class={this.getMenuClasses()}
          style={{ maxHeight: `${this.maxHeight}px` }}
        >
          {this.items.map((item, index) => (
            <div
              key={item.id}
              class={this.getItemClasses(index, item)}
              onMouseEnter={() => this.handleItemMouseEnter(index)}
              role="option"
              aria-selected={index === this.selectedIndex ? 'true' : 'false'}
              aria-disabled={item.disabled ? 'true' : 'false'}
            >
              <div
                class="item-content"
                onClick={() => this.handleItemClick(item)}
                onMouseDown={this.handleMouseDown}
              >
                <span class="item-text" title={item.label}>
                  {this.getHighlightedText(item.label)}
                </span>
                {this.multiSelect && item.selected && (
                  <span class="check-icon material-symbols-outlined">
                    check
                  </span>
                )}
              </div>
            </div>
          ))}
        </div>
      </Host>
    );
  }
}
